@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://unpkg.com/boxicons@2.0.9/css/boxicons.min.css);
.heading {
    font-weight: var(--font-weight-medium);
    margin: 0 !important;
}

.heading.xxs {
    color: var(--color-gray-500);
        font-size: var(--font-size-sm);
}
.heading.xs {
    color: var(--color-gray-600);
        font-size: var(--font-size-md);
}
.heading.sm {
    font-size: var(--font-size-xl);
    color: var(--color-gray-700);
}
.heading.md {
    font-size: var(--font-size-2xl);
    color: var(--color-gray-700);
}
.heading.lg {
    font-size: var(--font-size-3xl);
    color: var(--color-gray-700);
}

.heading.light {
    color: var(--color-gray-400);
}

.heading.center {
    text-align: center;
}
.data-table {
  border-radius: var(--border-radius-3xl);
  padding: 1.5rem 1rem;
  background-color: var(--white);
  box-shadow: var(--box-shadow-2xl);
  width: 100%;
}


.show-stats {
}

.show-stats__container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(10rem, 1fr));
  overflow-x: auto;
  grid-gap: 0.8rem;
  gap: 0.8rem;
}

.stats-card {
  padding: 0.7em;
  display: flex;
  justify-content: space-between;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  border-radius: var(--border-radius-2xl);
  border: solid 3px var(--color-secondary);
  background-color: white;
}

.stats-card__info {
    padding-top: 0.5rem;
  display: flex;
  flex-direction: column;
  grid-gap: 0.3rem;
  gap: 0.3rem;
  white-space: nowrap;
}

.icon-atom {
  background-color: var(--color-primary);
  color: var(--white);
  border-radius: var(--border-radius-lg);
  padding: 0.2rem;
}

.icon-atom.green {
  background-color: var(--color-green-400);
}

.icon-atom.red {
  background-color: var(--color-red-400);
}

.icon-atom.sm {
  font-size: var(--font-size-xl);
}

.icon-atom.md {
  font-size: var(--font-size-3xl);
}

.icon-atom.lg {
  font-size: var(--font-size-4xl);
}

.icon-atom.rounded {
  border-radius: 50%;
}

.table-page-template {
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.table-page-template__col-1 {
  flex: 100% 1;
}

.table-page-template__col-1 .header {
  padding: 1rem;
}

.table-page-template__col-1 .body {
  padding: 1rem;
  width: 100vw;
}

.table-page-template__col-2 {
  flex: 100% 1;
  padding: 1rem;
}

.button-icon {
    transition: all 0.1s;
    cursor: pointer;
}

.button-icon.primary {
    color: var(--color-text-brown);
}

.button-icon.primary:hover {
    -webkit-filter: brightness(1.1);
            filter: brightness(1.1);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.button-icon.secondary {
    color: var(--color-text-ligth);
}

.button-icon.secondary:hover {
    color: var(--color-gray);
}


.buttonWidthIcon {
  width: 100%;
  padding: 0.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: var(--border-radius-2xl);
  cursor: pointer;
  transition: all 0.1s;
}

/* ===== Button Categoria ===== */
.buttonWidthIcon.categoria {
  background-color: white;
  border: solid 3px var(--color-secondary);
  color: var(--color-text-brown);
  font-size: 400;
}

.buttonWidthIcon.categoria .icon {
  /* color: var(--color-text-ligth); */
  color: var(--color-primary-light);
}

.buttonWidthIcon.categoria.active {
  font-weight: 500;
  background-color: var(--color-secondary);
  border: solid 3px var(--color-primary-light);
}

.buttonWidthIcon.categoria.active .icon {
  color: var(--color-text-brown);
}

.buttonWidthIcon.categoria:hover {
  font-weight: 500;
  background-color: var(--color-secondary);
  border: solid 3px var(--color-primary-light);
}

.buttonWidthIcon.categoria:hover .icon {
  color: var(--color-text-brown);
}

/* ===== Button Menu ===== */

.buttonWidthIcon.menu {
  color: var(--color-text-ligth);
}
.buttonWidthIcon.menu:hover {
  font-weight: 500;
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
  color: var(--color-gray);
}

.buttonWidthIcon.menu.active {
  background-color: var(--color-primary);
  color: var(--white);
}

.buttonWidthIcon.menu.active:hover {
  color: var(--white);
}



.categories-container {
    /* background-color: red; */
    display: grid;
    grid-template-columns: repeat(auto-fill, 4.8rem);
    grid-gap: 1em;
    gap: 1em;
}
.product-card {
  background-color: var(--white);
  border-radius: var(--border-radius-xl);
  overflow: hidden;
  box-shadow: var(--box-shadow-md);
  display: flex;
  flex-direction: column;
  text-align: center;
}

.product-card__img {
  height: 50%;
}

.product-card__info {
  min-height: 50%;
  grid-gap: 0em;
  gap: 0em;
  padding: 0em 0.4rem 0.6rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .product-card__img {
    height: 55%;
  }
  .product-card__info {
    min-height: 45%;
    padding: 0.6rem 0.4rem 0.6rem;
  }
}
@media screen and (min-width: 1024px) {
  .product-card__info {
    padding: 0.6rem 0.6rem 0.7rem;
  }
}

.button {
  background-color: red;
  border: solid 2px transparent;
  border-radius: var(--border-radius-xl);
  font-weight: 600;
  width: 100%;
}

/* ===== SIZE ===== */
.button.sm {
  font-size: var(--font-size-sm);
  padding: var(--spacing-4) var(--spacing-6);
}
.button.md {
  font-size: var(--font-size-base);
  padding: var(--spacing-7) var(--spacing-14);

}
.button.lg {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-bold);
  padding: var(--spacing-8) var(--spacing-14);

}

/* ===== TYPE ===== */
.button.primary {
    background-color: var(--color-primary);
    color: var(--white);
}
.button.primary:hover {
    -webkit-filter: brightness(1.15);
            filter: brightness(1.15);
}
.button.primary:active {
  background-color: var(--white);
  color: var(--color-primary);
  border-color: var(--color-primary);
}


.button.secondary {
    background-color: var(--white);
    border-color: var(--color-text-brown);
    color: var(--color-text-brown);
}
.button.secondary:hover {
    background-color: var(--color-primary);
    border-color: transparent;
    color: var(--white);
}

.img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.products-container {
  width: 100%;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, minmax(10rem, 1fr));
  grid-gap: 1em;
  gap: 1em;
}

@media screen and (min-width: 768px) {
  .products-container {
    grid-template-columns: repeat(auto-fill, minmax(12rem, 1fr));
    grid-gap: 1.5em;
    gap: 1.5em;
  }
}

.cart-article {
    display: flex;
}

.cart-article__img {
    align-self: center;
    width: 4em;
    height: 4em;
    border-radius: var(--border-radius-2xl);
    overflow: hidden;
}

.cart-article__info {
    flex: 1 1;
    margin-left: 0.5em;
}

.cart-article__info div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50%;
}


.edit-quantity {
    display: flex;
    align-items: center;
}

.list-cart-items {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    height: 100%;
    overflow-y: auto;
    padding: 1rem 0.8rem 1rem 0;
}
.list-container {
    background-color: var(--white);
    width: 85%;
    margin-top: 0.2em;
    margin-left: 0.4em;
    padding: 0.5em 0;
    box-shadow: var(--box-shadow-md);
    position: absolute;
    top: 100%;
}

.list-item {
    padding: 0.4em 0.9em;
}

.list-item:hover {
    background-color: var(--color-primary);
    color: var(--white);
}
.search-and-add-customer {
    background-color: var(--white);
    display: flex;
    align-items: center;
    padding: 0.1em 0.2em;
    border-radius: var(--border-radius-full);
    box-shadow: var(--box-shadow-md);
    position: relative;
}

.search-and-add-customer .icon {
    color: var(--color-text-brown);
}

.search-and-add-customer .icon:first-child {
    padding-left: 0.2em;
    margin-right: 0.1em;
}

.search-and-add-customer input {
    padding: 0.5em 0;
    height: 100%;
    width: 100%;
    outline: none;
    border: none;
    font-weight: var(--font-weight-medium);
    font-size: var(--font-size-base);
}
.shopping-cart {
    background-color: white;
    box-shadow: var(--box-shadow-md);
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    padding: 1rem;
}

.shopping-cart__header {
    height: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 1rem;
}

.shopping-cart__body {
    overflow: hidden;
    flex: 1 1;
}

.shopping-cart__body__header {
    border-bottom: var(--border-bottom-1);
    height: 2.2rem;
}


.shopping-cart__body__container {
    overflow-y: auto;
    height: calc(100% - 2.2rem);
    display: flex;
    flex-direction: column;
    grid-gap: 0.9rem;
    gap: 0.9rem;
}


.shopping-cart__footer {
    background-color: white;
    z-index: 9;
    height: 8rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 1rem;
    border-top: var(--border-bottom-1);
}

.shopping-cart__body__header, .shopping-cart__footer__header {
    display: flex;
    justify-content: space-between;
}
.pos-template {
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
}

.pos-template__main-container {
  height: 100%;
  /* padding-top: 2.5rem; */
}

.btn-showShoppingCart {
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.pos-template__shoppingCart {
  height: 100vh;
  background-color: purple;
  width: 100%;
  position: fixed;
  top: 0;
  right: -100%;
  transition: all 0.3s ease-in-out;
  z-index: 99;
}

.pos-template__shoppingCart.show {
  right: 0;
}

.pos-template .btn-shoppingCart {
  position: absolute;
  right: 1rem;
  top: 1rem;
}

/* ===== POS TEMPLATE HEADER ===== */

.pos-template__main-container .header {
  padding: 1rem;
}

.pos-template__main-container .body {
  padding: 1rem;
}



.sidebar {
  background-color: var(--white);
  padding: 1rem;
  height: 100%;
  box-shadow: var(--box-shadow-xl);
}

.sidebar__header {
  height: 20%;
  padding: 1rem;
}

.sidebar__body {
  height: 80%;
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main-layout {
  position: relative;
  min-height: 100vh;
  background-color: #f9f9fb;
}

.main-layout__sidebar {
  position: fixed;
  height: 100vh;
  width: 9rem;
  left: -100%;
  top: 0;
  transition: all 0.3s ease-in-out;
  z-index: 9;
}

.main-layout__sidebar.show {
  left: 0%;
}

.main-layout__btn-showSidebar {
  position: absolute;
  left: 0.5rem;
  top: 0.5rem;
  z-index: 9;
}

.main-layout__container {
  min-height: 100vh;
  display: flex;
  padding-top: 3rem;
}

@media screen and (min-width: 1024px) {
  .main-layout {
    margin-left: 9rem;
  }
  .main-layout__sidebar {
    left: 0;
  }

  .main-layout__btn-showSidebar {
    display: none;
  }
  .main-layout__container {
    padding-top: 2rem;
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

:root {
    --color-primary: #814c3b;
    --color-primary-light: #ae9f9b;
    --color-secondary: #f6efef;
    --color-text-brown: #7f635a;
    --color-text-ligth: #b6b6b6;
    --width-sidebar: 10vw;
    --bg-color: #f9f9fb;
  
    --border-radius: 22%;
    --white: #FFFFFF;
    --color-gray: rgb(128, 128, 128);
}
  

* {
  margin: 0; 
  padding: 0;
  box-sizing: border-box;
}

p * {
  margin: 0;
}

body {
  font-family: "Roboto", sans-serif;
}

li {
  list-style: none;
}

/* VERTICAL SPACE  */
.vertical-space-1 {
  display: flex;
  flex-direction: column;
  grid-gap: 0.7rem;
  gap: 0.7rem;
}
.vertical-space-2 {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}
.vertical-space-3 {
  display: flex;
  flex-direction: column;
  grid-gap: 1.3em;
  gap: 1.3em;
}

:root {
  /* ===== BORDER RADIUS ===== */
  --border-radius-sm: 0.125rem;
  --border-radius-md: 0.375rem;
  --border-radius-lg: 0.5rem;
  --border-radius-xl: 0.75rem;
  --border-radius-2xl: 1rem;
  --border-radius-3xl: 1.5rem;
  --border-radius-full: 9999px;

  /* ===== FONT SIZE ===== */
  --font-size-xs: 0.75rem;
  --font-size-sm: 0.875rem;
  --font-size-base: 1rem;
  --font-size-lg: 1.125rem;
  --font-size-xl: 1.25rem;
  --font-size-2xl: 1.5rem;
  --font-size-3xl: 1.875rem;
  --font-size-4xl: 2.25rem;
  --font-size-5xl: 3rem;
  --font-size-6xl: 3.75rem;
  --font-size-7xl: 4.5rem;
  --font-size-8xl: 6rem;
  --font-size-9xl: 8rem;

  /* ===== LINE HEIGHT ===== */
  --line-height-xs: 1rem;
  --line-height-sm: 1.25rem;
  --line-height-lg: 1.75rem;
  --line-height-xl: 1.75rem;

  /* ===== FONT WEIGHT ===== */
  --font-weight-light: 300;
  --font-weight-normal: 400;
  --font-weight-medium: 500;
  --font-weight-bold: 700;

  /* ===== COLORS ===== */
  /* GRAY */
  --color-gray-50: #fafafa;
  --color-gray-100: #f4f4f5;
  --color-gray-200: #e4e4e7;
  --color-gray-300: #d4d4d8;
  --color-gray-400: #a1a1aa;
  --color-gray-500: #71717a;
  --color-gray-600: #52525b;
  --color-gray-700: #3f3f46;
  --color-gray-800: #27272a;
  --color-gray-900: #18181b;

  /* RED */
    --color-red-50: #fef2f2;
    --color-red-100: #fee2e2;
    --color-red-200: #fecaca;
    --color-red-300: #fca5a5;
    --color-red-400: #f87171;
    --color-red-500: #ef4444;
    --color-red-600: #dc2626;
    --color-red-700: #b91c1c;
    --color-red-800: #991b1b;
    --color-red-900: #7f1d1d;


  /* GREEN */
    --color-green-50: #f0fdf4;
    --color-green-100: #dcfce7;
    --color-green-200: #bbf7d0;
    --color-green-300: #86efac;
    --color-green-400: #4ade80;
    --color-green-500: #22c55e;
    --color-green-600: #16a34a;
    --color-green-700: #15803d;
    --color-green-800: #166534;
    --color-green-900: #14532d;

  





  /* ===== SPACING ===== */
  --spacing-1: 0.125rem;
  --spacing-2: 0.25rem;
  --spacing-3: 0.375rem;
  --spacing-4: 0.5rem;
  --spacing-5: 0.625rem;
  --spacing-6: 0.75rem;
  --spacing-7: 0.875rem;
  --spacing-8: 1rem;
  --spacing-9: 1.25rem;
  --spacing-10: 1.5rem;
  --spacing-11: 1.75rem;
  --spacing-12: 2rem;
  --spacing-13: 2.25rem;
  --spacing-14: 2.5rem;

  /* ===== BOX SHADOW ===== */
  --box-shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --box-shadow-DEFAULT: 0 1px 3px 0 rgba(0, 0, 0, 0.1),
    0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --box-shadow-md: 0 3px 10px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --box-shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --box-shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
  --box-shadow-2xl: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  --box-shadow-inner: inset 0 2px 4px 0 rgba(0, 0, 0, 0.06);

  /* ===== BORDERS ===== */
  --border-bottom-1: solid 1px var(--color-gray-200);
  --border-1: solid 1px var(--color-gray-200);
}

